import { Box, Breadcrumbs, Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, List, ListItem, ListItemSecondaryAction, ListItemText, Radio, Select, TextField, Typography } from "@material-ui/core";
import { Link } from "gatsby";
import * as React from "react";
import ajax, { useFetcher } from "shared/components/ajax";
import save, { useUserInfo } from "shared/components/save";
import { useModalTheme } from "shared/components/style";
import Defer from "shared/components/ui/Defer";
import Navbar from "shared/components/ui/Navbar";
import { winalert, winconfirm } from "shared/components/util";
import AddIcon from '@material-ui/icons/Add';
import PlaceIcon from '@material-ui/icons/Place';


const Prompt = ({ inquiry, open, onClose, onAction }) => {
    const user = useUserInfo();
    const modalTheme = useModalTheme();
    const [label, setLabel] = React.useState('');
    const [name, setName] = React.useState('');
    const [phone, setPhone] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [prov, setProv] = React.useState(null);
    const [kab, setKab] = React.useState(null);
    const [kec, setKec] = React.useState(null);
    const [desa, setDesa] = React.useState(null);
    const [street, setStreet] = React.useState('');
    const [complement, setComplement] = React.useState('');
    const [postal, setPostal] = React.useState('');
    const [lprov, lkab, lkec, ldesa] = [
        useFetcher('/desa/geolookup'),
        useFetcher(prov && '/desa/geolookup?id=' + prov),
        useFetcher(kab && '/desa/geolookup?id=' + kab),
        useFetcher(kec && '/desa/geolookup?id=' + kec),
    ];
    const [nprov, nkab, nkec, ndesa] = [
        React.useMemo(() => prov && lprov ? lprov.descendants.find(x => x.id == prov).name : '', [prov, lprov]),
        React.useMemo(() => kab && lkab ? lkab.descendants.find(x => x.id == kab).name : '', [kab, lkab]),
        React.useMemo(() => kec && lkec ? lkec.descendants.find(x => x.id == kec).name : '', [kec, lkec]),
        React.useMemo(() => desa && ldesa ? ldesa.descendants.find(x => x.id == desa).name : '', [desa, ldesa]),
    ];
    React.useEffect(() => {
        if (inquiry) {
            var geo = inquiry.geocode;
            if (!geo)
                geo = user ? (user.account.geocode || user.nik.substr(0, 6)) : '';
            setLabel(inquiry.label);
            setName(inquiry.name || user.name);
            setPhone(inquiry.phone || user.phone);
            setEmail(inquiry.email);
            setProv(geo.substr(0, 2));
            setKab(geo.substr(0, 4));
            setKec(geo.substr(0, 6));
            setDesa(geo.length > 6 ? geo : '');
            setStreet(inquiry.street);
            setComplement(inquiry.complement);
            setPostal(inquiry.postal);
        }
    }, [inquiry]);
    const save = (data) => {
        onAction({
            action: 'update',
            label,
            name,
            phone,
            email,
            geocode: desa,
            country: 'Indonesia',
            state: nprov,
            region: nkab,
            city: nkec,
            district: ndesa,
            street,
            complement,
            coordinate: null,
            postal,
        });
    };
    const del = () => {
        winconfirm('Yakin ingin menghapus alamat ini?').then(y => {
            if (y) onAction({ action: 'delete' });
        });
    };
    const def = () => {
        winconfirm('Yakin ingin alamat ini default?').then(y => {
            if (y) onAction({ action: 'makeDefault' });
        });
    };
    return (
        <Dialog
            open={open}
            onClose={onClose}
            scroll="body"
            fullWidth
            maxWidth={false}
        >
            <DialogTitle>
                <Box display='flex'>
                    {inquiry?.id || '' ? 'Edit Alamat' : 'Masukkan Alamat'}
                    <Box flexGrow={1} />
                    {
                        inquiry?.id && inquiry?.id !== user?.account.address &&
                        <Button onClick={def} variant="outlined" color="secondary">
                            Jadikan Default
                        </Button>
                    }
                </Box>
            </DialogTitle>
            <DialogContent>
                {<List>
                    <ListItem>
                        <ListItemText primary="Label" secondary="Rumah/kantor/dsb" />
                        <ListItemSecondaryAction>
                            <FormControl style={{ minWidth: '40vw' }}>
                                <TextField value={label} onChange={(e) => setLabel(e.target.value)} />
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Nama" secondary="Nama lengkap" />
                        <ListItemSecondaryAction>
                            <FormControl style={{ minWidth: '40vw' }}>
                                <TextField value={name} onChange={(e) => setName(e.target.value)} />
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Nomor HP" secondary="Nomor HP aktif" />
                        <ListItemSecondaryAction>
                            <FormControl style={{ minWidth: '40vw' }}>
                                <TextField value={phone} onChange={(e) => setPhone(e.target.value)} />
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem divider>
                        <ListItemText primary="Email" secondary="Email aktif (opsional)" />
                        <ListItemSecondaryAction>
                            <FormControl style={{ minWidth: '40vw' }}>
                                <TextField value={email} onChange={(e) => setEmail(e.target.value)} />
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Negara" />
                        <ListItemSecondaryAction>
                            <FormControl disabled style={{ minWidth: '40vw' }}>
                                <TextField value="Indonesia" />
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Provinsi" />
                        <ListItemSecondaryAction>
                            <FormControl disabled={!lprov} style={{ minWidth: '40vw' }}>
                                <Select native value={prov} onChange={(x) => { setProv(x.target.value); setKab(null); setKec(null); setDesa(null) }}>
                                    <option value=""></option>
                                    {lprov ? lprov.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                </Select>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Kabupaten" />
                        <ListItemSecondaryAction>
                            <FormControl disabled={!lkab} style={{ minWidth: '40vw' }}>
                                <Select native value={kab} onChange={(x) => { setKab(x.target.value); setKec(null); setDesa(null) }}>
                                    <option value=""></option>
                                    {lkab ? lkab.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                </Select>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Kecamatan" />
                        <ListItemSecondaryAction>
                            <FormControl disabled={!lkec} style={{ minWidth: '40vw' }}>
                                <Select native value={kec} onChange={(x) => { setKec(x.target.value); setDesa(null) }}>
                                    <option value=""></option>
                                    {lkec ? lkec.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                </Select>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Desa" />
                        <ListItemSecondaryAction>
                            <FormControl disabled={!ldesa} style={{ minWidth: '40vw' }}>
                                <Select native value={desa} onChange={(x) => { setDesa(x.target.value) }}>
                                    <option value=""></option>
                                    {ldesa ? ldesa.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                </Select>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Jalan" secondary="No. RT/RW/Blok" />
                        <ListItemSecondaryAction>
                            <FormControl style={{ minWidth: '40vw' }}>
                                <FormControl style={{ minWidth: '40vw' }}>
                                    <TextField value={street} onChange={(e) => setStreet(e.target.value)} />
                                </FormControl>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Kompleks" secondary="Nama Ruko/Gedung" />
                        <ListItemSecondaryAction>
                            <FormControl style={{ minWidth: '40vw' }}>
                                <FormControl style={{ minWidth: '40vw' }}>
                                    <TextField value={complement} onChange={(e) => setComplement(e.target.value)} />
                                </FormControl>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Kode Pos" />
                        <ListItemSecondaryAction>
                            <FormControl style={{ minWidth: '40vw' }}>
                                <FormControl style={{ minWidth: '40vw' }}>
                                    <TextField value={postal} onChange={(e) => setPostal(e.target.value)} />
                                </FormControl>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <div>
                            <ListItemText primary="Contoh Penulisan Alamat" />
                            <Typography style={{ marginLeft: '40px' }}>
                                <b>{name}</b><br />
                                {email} / {phone},<br />
                                {complement}, {street},<br />
                                {ndesa}, {nkec}, {nkab}, <br />
                                {nprov}, {postal}<br />
                            </Typography>
                        </div>
                    </ListItem>
                </List>}
            </DialogContent>
            <DialogActions >
                {
                    inquiry?.id && <Button onClick={del} variant="outlined" color="secondary">
                        Hapus
                    </Button>
                }
                <Box flexGrow={1} />
                <Button onClick={onClose} color="primary">
                    Batal
                </Button>
                <Button onClick={save} variant="contained" color="primary" disabled={!(ndesa && label && name && street && phone && parseInt(postal) && complement)}>
                    Simpan
                </Button>
            </DialogActions>
        </Dialog>
    );
}

const Page = () => {
    const user = useUserInfo();
    const [rel, setRel] = React.useState(1);
    const list = useFetcher(`/market/address/?refresh=${rel}`);
    const [open, setOpen] = React.useState(null);
    const add = () => {
        setOpen({
            id: null,
            label: '',
            name: '',
            phone: '',
            email: '',
            geocode: '',
            country: '',
            state: '',
            region: '',
            city: '',
            district: '',
            street: '',
            complement: '',
            coordinate: null,
            postal: '',
        });
    }
    const onSave = (data) => {
        ajax.post('/market/address/' + (open?.id || data.id || ''), data).then(() => {
            winalert('Berhasil disimpan');
            save.userInfo = null;
            setRel(rel + 1);
            setOpen(null);
        }).catch(x => {
            alert(x.response?.data.message || x.message);
        })
    }
    return (
        <Defer data={user && list}>
            {() => <div>
                <Navbar />
                <List>
                    <ListItem>
                        <Breadcrumbs>
                            <Link color="inherit" to="/user/profile/">
                                Pengaturan Akun
                            </Link>
                            <Typography color="textPrimary">Atur Data Alamat</Typography>
                        </Breadcrumbs>
                        <ListItemSecondaryAction>
                            <Button startIcon={<AddIcon />} onClick={add} variant="contained" color="primary">
                                Tambah Alamat
                            </Button>
                        </ListItemSecondaryAction>
                    </ListItem>
                    {
                        list.data.map(x => <ListItem key={x.id} button onClick={() => setOpen(x)}>
                            <ListItemText primary={<>{x.label}{x.coordinate && <PlaceIcon fontSize="small" />}</>} secondary={
                                <>{`${x.name}, ${x.district},`}<br />{` ${x.city}, ${x.region}, ${x.postal}`}</>
                            } />
                            <ListItemSecondaryAction>
                                <Radio checked={user.account.address == x.id} onChange={(y) => {
                                    if (y) winconfirm('Yakin ingin alamat ini default?').then(y => {
                                        if (y) {
                                            onSave({ id: x.id, action: 'makeDefault' });
                                        }
                                    });
                                }} />
                            </ListItemSecondaryAction>
                        </ListItem>)
                    }
                    {
                        list.data.length === 0 && <ListItem>
                            <Box fontStyle="italic" width="100%" padding="20px">
                                <Typography align="center">
                                    Belum ada data alamat
                                </Typography>
                            </Box>
                        </ListItem>
                    }
                </List>
                <Prompt key={open?.id || 'new'} open={open !== null} onAction={onSave} inquiry={open} onClose={() => setOpen(null)} />
            </div>
            }
        </Defer>
    );
}

export default Page
